body {
    background-color: black;
    color: white;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.painting-title {
    text-align: center;
}

.artist {
    font-size: 100%;
}

.artwork {
    font-size: 200%
}

.photo-preview-container {
    margin-left: auto;
    margin-right: auto;
    width: 80%;
}

.photo-preview {
    width: 100%;
    margin-bottom: 10%;
}

.animation {
    border: double 4px purple;
}

.form {
    padding: 1%;
}

.column {
    float: left;
    width: 50%;
}

.row:after {
    content: "";
    display: table;
    clear: both;
}

.graph-title {
    font-size: 25px;
}

.graph {
    border: solid 2px white;
    padding: 2%;
}

.red-title {
    font-size: 25px;
    color: red;
}

.red {
    border: solid 2px red;
    padding: 2%;
}

.green-title {
    font-size: 25px;
    color: green;
}

.green {
    border: solid 2px green;
    padding: 2%;
}

.blue-title {
    font-size: 25px;
    color: blue;
}

.blue {
    border: solid 2px blue;
    padding: 2%;
}

.alpha-title {
    font-size: 25px;
}

.alpha {
    border: solid 2px white;
    padding: 2%;
}

.error-message {
    color: #ff0000a0;
    font-size: 10px;
}

.title {
    text-align: center;
    font-size: 400%;
    font-weight: 250;
}

.animations-title {
    text-align: center;
    color: rgba(200, 50, 210, 0.3);
    font-size: 400%;
    font-weight: 250;
}